.hero {
  position: relative;
}

.hero .container {
  padding: 0;
}

.hero h1 {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.hero__swiper {
  position: relative;

  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
  min-height: 800px;

  @include vp-1199 {
    min-height: 1024px;
  }

  @include vp-767 {
    min-height: 568px;
  }
}

.hero__swiper-pagination {
  position: absolute;

  @include vp-1199 {
    left: 50%;
    transform: translate(-50%);
  }
}

.hero__swiper-slide {
  position: relative;
  min-height: 800px;
  background-size: cover;
  background-repeat: no-repeat;

  &--caucasus {
    background-image: url("../img/caucasus-desktop.jpg");
  }

  &--kamchatka {
    background-image: url("../img/kamchatka-desktop.jpg");
  }

  &--altai {
    background-image: url("../img/altai-desktop.jpg");
  }


  @include vp-1199 {
    min-height: 1024px;
  }

  @include vp-767 {
    min-height: 568px;
  }
}

.hero__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: $hero-gradient;
  z-index: 1;
  padding: 200px 120px 80px;

  @include vp-1199 {
    padding: 212px 45px 90px;
  }

  @include vp-767 {
    padding: 132px 15px 48px;
  }
}

.hero__btn {
  margin: 0 0 134px;

  padding: 20px 20px;
  border-radius: 6px;
  background-color: $color-additional;

  color: $color-white;
  text-align: center;
  font-family: $ff-montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px; /* 100% */

  @include vp-1199 {
    margin: 0 0 45px;
  }

  @include vp-767 {
    margin: 0 0 125px;
  }
}

.hero__info h2 {
  margin: 0 0 25px;
  padding: 0;
  width: 944px;

  color: $color-white;
  font-family: $ff-montserrat;
  font-size: 77px;
  font-style: normal;
  font-weight: 800;
  line-height: 77px; /* 100% */

  @include vp-1199 {
    margin: 0 0 15px;
    width: 100%;

    text-align: center;
    font-size: 56px;
    line-height: 56px; /* 100% */
  }

  @include vp-767 {
    font-size: 26px;
    line-height: 26px; /* 100% */
  }
}

.hero__info p {
  margin: 0 0 35px;
  padding: 0;
  width: 464px;

  color: $color-white;
  font-family: $ff-montserrat;
  font-size: 17px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 141.176% */

  @include vp-1199 {
    margin: 0 0 25px;
    width: 500px;
    text-align: center;
  }

  @include vp-767 {
    margin: 0 0 60px;
    width: 100%;
    font-size: 15px;
    line-height: 20px; /* 133.333% */
  }
}

.hero__desc {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;

  @include vp-1199 {
    display: none;
  }
}

.hero__desc svg {
  margin-right: 8px;
  color: $color-white;
}

.hero__desc span {
  padding: 13px 19px;
  display: flex;
  align-items: center;

  border: 1px solid $color-white-opacity-50;
  color: $color-white;
  font-family: $ff-montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px; /* 133.333% */
}
